<template>
  <div class="quest overflowyauto">
    <div class="quest-inner">
      <div class="wallet" @click="openWallet(1)"></div>
      <div class="user">
        <div class="user-img">
          <img v-if="userAvatar" :src="userAvatar" alt="" srcset="">
          <img v-else src="@/assets/img/friends/user-img.svg" alt="" srcset="">
        </div>
        <div class="user-name" @click="setSwitchNetwork()">{{userName}}</div>
        <div class="user-integral" @click="getUserData()">
          <img src="@/assets/img/icons/integral.svg" alt="" srcset="">
          <span v-if="taskPoints > 0">+{{taskPoints}}</span>
        </div>
        <div class="user-bg"></div>
      </div>
      <div class="quest-title">Daily Tasks</div>
      <div class="list">
        <ul>
          <li v-for="(item,index) in dailyList" :key="index" @click="dailyTasks(item)">
            <div class="listimg" :style="{ backgroundImage: 'url(' + item.avatar + ')' }"></div>
            <div class="name">
              <div class="title">{{item.Task?.name}}</div>
              <div class="integral">{{item.Task?.points}}</div>
            </div>
            <div class="listtype"><div :class="{'active':item.TaskFinished?.isClaimed}"></div></div>
          </li>
        </ul>
      </div>
      <div class="quest-title">Basic Tasks</div>
      <div class="list">
        <ul>
          <li v-for="(item,index) in basicList" :key="index" @click="dailyTasks(item)">
            <div class="listimg" :style="{ backgroundImage: 'url(' + item.avatar + ')' }"></div>
            <div class="name">
              <div class="title">{{item.Task?.name}}</div>
              <div class="integral">{{item.Task?.points}}</div>
            </div>
            <div class="listtype"><div :class="{'active':item.TaskFinished?.isClaimed}"></div></div>
          </li>
        </ul>
      </div>
    </div>
    <nav-menu on="quest" :showValue="false"></nav-menu>
    <all-wallet :showWallet="showWallet" @childClick="updateParentValue"></all-wallet>
  </div>
</template>

<script>
import { ref } from 'vue';
import NavMenu from '@/components/NavMenu.vue'
import AllWallet from '@/components/AllWallet.vue'
import RewardDistributorAbi from '@/abis/RewardDistributorAbi'
import { getUserAgent } from '@/lib/isMobileDevice'
import { useWeb3Modal, useWeb3ModalAccount, useSwitchNetwork, useWeb3ModalProvider } from '@web3modal/ethers/vue'
import { BrowserProvider, Contract, formatUnits } from 'ethers'
import {get, post} from "@/lib/API";


const routeUrl = process.env.ROUTE_URL || '../';

export default {
  name: 'Quest',
  components: {
    NavMenu,
    AllWallet
  },
  data() {
    return {
      dailyList: [],
      basicList: [],
      localDailyList : [
        { name: 'Daily Check-in',type: 'Daily', avatar: routeUrl + 'assets/img/icons/time.svg',id: 1, num: 100, actrve: true, on: false, onavatar: routeUrl + 'assets/img/icons/time.png' },
        { name: 'Turn to official website',type: 'Turn', avatar: routeUrl + 'assets/img/quest/turn.svg',id: 2, num: 200, actrve: false, on: false, onavatar: routeUrl + 'assets/img/icons/turn.png' },
        { name: 'Jumper bot user',type: 'Jumper', avatar: routeUrl + 'assets/img/quest/jumper.svg',id: 3, num: 300, actrve: true, on: false, onavatar: routeUrl + 'assets/img/icons/jumper.png' },
        { name: 'Daily Share',type: 'Share', avatar: routeUrl + 'assets/img/quest/daily.svg',id: 4, num: 400, actrve: false, on: false, onavatar: routeUrl + 'assets/img/icons/daily.png' },
      ],
      localBasicList : [
        { name: 'Connect Wallet',type: 'Wallet', avatar: routeUrl + 'assets/img/quest/wallet.svg',id: 1, num: 10, prize: 10, actrve: false, on: false, onavatar: routeUrl + 'assets/img/icons/wallet.png' },
        { name: 'Join X Group',type: 'JoinGroup', avatar: routeUrl + 'assets/img/quest/join.svg',id: 2, num: 50, prize: 60, actrve: true, on: false, onavatar: routeUrl + 'assets/img/icons/join.png' },
        { name: 'Follow on Twitter',type: 'Twitter', avatar: routeUrl + 'assets/img/quest/twitter.svg',id: 3, num: 100, prize: 130, actrve: false, on: false, onavatar: routeUrl + 'assets/img/icons/twitter.png' },
        { name: 'Retweet a Post',type: 'Retweet', avatar: routeUrl + 'assets/img/quest/retweet.svg',id: 3, num: 100, prize: 130, actrve: true, on: false, onavatar: routeUrl + 'assets/img/icons/retweet.png' },
      ],
      walletAddress: '',
      getModal: useWeb3Modal(),
      showWallet: false,
      userAgent: '',
      isMobileDevice: getUserAgent(),
      userAddress:localStorage.getItem('userAddress') || '',
      userName:'',
      userAvatar:'',
      taskPoints: 0,
      getWalletItem: '',
    };
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.verifyInitData();
      this.getMyTaskDailyList();
      this.getMyTaskBasicList();
      this.getTgUserData();
    },
    // 打开钱包
    openWallet(type) {
      // 浏览器用这个
      //getModal.open();
      // 移动端用这个
      console.log(this.isMobileDevice)
      if (this.isMobileDevice) {
        this.showWallet = true;
      } else {
        if(this.userAddress) {
          this.$toast.success(this.userAddress);
        } else {
          const { address } = useWeb3ModalAccount()
          if(address.value) {
            this.connectWallet(address.value, type)
          } else {
            this.getModal.open();
          }
        }
      }
    },
    // 获取用户数据
    async getUserData() {
      const { address, chainId, isConnected } = useWeb3ModalAccount()
      localStorage.setItem('userAddress', address.value)
      console.log(address.value, chainId.value, isConnected.value)
    },
    // 切换链
    async setSwitchNetwork() {
      try {
        const chainId = 1
        const { switchNetwork } = useSwitchNetwork()
        switchNetwork(chainId)
      } catch (err) {
        console.log(err)
      }
    },
    updateParentValue(value) {
      this.showWallet = value;
      console.log('showWallet', value);
    },
    //关联钱包
    connectWallet(walletAddress) {
      post('/tguser/bind_wallet', { walletAddress: walletAddress})
        .then(async response => {
          if (response.code === 0) {
            localStorage.setItem('userAddress', walletAddress)
            const res = await get("/task/finish", { task_id: this.getWalletItem.Task?.ID });
            if (res.code === 0) {
              this.init();
              this.$toast.success(`You get ${this.getWalletItem.Task?.points} points!`);
            } else {
              if (walletAddress) {
                this.$toast.error(walletAddress);
              } else {
                this.$toast.error(res.msg);
              }
            }
          }
        })
        .catch(error => {
          console.log(error)
        });
    },
    //获取列表数据
    verifyInitData() {
      
      get('/distribution/getDistributionList', { page: 1, pageSize: 10 })
      .then(response => {

      })
      .catch(error => {

      });

    },
    //每日任务 单项任务
    dailyTasks(item) {
      if (!item.TaskFinished?.isClaimed) {
        console.log(item.Task.ID)
        this.upDateFinish(item)
      } else {
        this.$toast.error(`Please do not claim again!`);
      }
    },
    getMyTaskDailyList() {
      get('/task/getMyTaskList', { page: 1, pageSize: 10, type: 'daily' })
        .then(response => {
          if (response.code === 0) {
            const list = response.data.list || []
            list.map(item => {
              this.localDailyList.map(localItem => {
                if(item.Task.name === localItem.name){
                  item.avatar = localItem.avatar
                }
              })
            })
            this.dailyList = list
          }
        })
        .catch(error => {
          console.log(error)
        });
    },
    getMyTaskBasicList() {
      get('/task/getMyTaskList', { page: 1, pageSize: 10, type: 'basic' })
        .then(response => {
          if (response.code === 0) {
            const list = response.data.list || []
            list.map(item => {
              this.localBasicList.map(localItem => {
                if (item.Task?.name === 'Connect Wallet') {
                  this.getWalletItem = item;
                }
                if(item.Task.name === localItem.name){
                  item.avatar = localItem.avatar
                }
              })
            })
            this.basicList = list
          }
        })
        .catch(error => {
          console.log(error)
        });
    },
    async getTgUserData() {
      const res = await get("/tguser/myTelegramUser");
      if (res.code === 0) {
        console.log(res.data);
        const user = res.data.user;
        this.userAvatar = user.avatar;
        this.taskPoints = user.task_points;
        this.userName = user.username;
      }
    },
    async upDateFinish(item) {
      console.log(item.Task?.name)
      switch (item.Task?.name) {
        case 'Connect Wallet':
          this.openWallet(2);
          break;
        default:
          const res = await get("/task/finish", { task_id: item.Task?.ID });
          if (res.code === 0) {
            this.init();
            this.$toast.success(`You get ${item.Task?.points} points!`);
          } else {
            this.$toast.error(res.msg);
          }
          break;
      }
    },
  }
}
</script>
<style lang="scss" scoped>
.quest-inner{
  position: relative;
  padding-bottom: 6rem;
  .wallet{
    position: absolute;top: 1rem;right: 1rem;
    width: 3rem;
    height: 3rem;
    background: url('@/assets/img/quest/wallet.svg') no-repeat center center/contain;
  }
  .user{
    width: 17rem;height: 17rem;
    text-align: center;
    margin: 0 auto;
    padding-top: 4.5rem;
    margin-bottom: 5rem;
    background: url('@/assets/img/icons/light.png') no-repeat center center/contain;
    position: relative;
    .user-img{
      position: relative;
      z-index: 1;left: 0;
      width: 8rem;height: 8rem;
      margin: 0 auto;
      border-radius: 50%;
      margin-bottom: 2rem;
      img{
        width: 100%;height: 100%;
        border-radius: 50%;
      }
    }
    .user-name{
      font-size: 1.5rem;
      font-weight: bold;
      height: 2rem;
    }
    .user-integral{
      width: 50%;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 3rem;
      img{
        width: 2.4rem;height: 2.4rem;
        margin-right: .6rem;
      }
      span{
        font-size: 1.2rem;
      }
    }
    .user-bg{
      width: 13rem;height: 13rem;
      position: absolute;top:50%;left: 50%;
      transform: translate(-50%,-50%);
      background: url('@/assets/img/ranking/move.png') no-repeat center center/contain;
	    animation: rotate 14s linear infinite;
    }
    @keyframes rotate {
      0% {
        transform: translate(-50%, -50%) rotate( 0deg);
      }
      100% {
        transform: translate(-50%, -50%) rotate(-360deg);
      }
    }
  }
  .quest-title{
    font-size: 1.3rem;font-weight: bold;
    padding-left: 1rem;font-style: italic;
  }
  .list{
    ul{
      padding: 1rem;
      li{
        padding: 1.2rem 1rem;
        border-radius: 1rem;
        margin: 1rem 0;
        background: #EAFFFF;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #161616;
        .listimg{
          width: 2.8rem;height: 2.8rem;
          background: url('@/assets/img/quest/wallet.png') no-repeat center center/contain;
        }
        .name{
          width: 70%;
          font-size: 1rem;
          font-weight: bold;
          .title{
            height: 2rem;line-height: 1.5rem;
          }
          .integral{
            padding-left: 2rem;
             background: url('@/assets/img/icons/integral-02.svg') no-repeat left center/contain;
          }
        }
        .listtype{
          width: 1.5rem;height: 1.5rem;border-radius: 50%;
          border: .2rem solid #161616;
          text-align: center;font-size: 1.1rem;
          font-weight: bold;
          padding: 2px;
          .active{
            background: #0C3DED;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
          }
        }
      }
    }
  }
}
</style>
