<template>
  <div class="friends">
    <div class="friends-inner overflowyauto">
      <div class="friends-header">
        <div class="friends-title">Friends List</div>
        <div class="friends-refresh" :class="{ 'updata-list': isUpdata }" @click="getMyFriendList(1)"></div>
      </div>
      <div class="friends-list" @scroll="handleScroll">
        <ul>
          <li v-for="(friend, index) in friends" :key="index">
            <div class="friends-avatar">
              <img v-if="friend.TelegramUser?.avatar" :src=friend.TelegramUser?.avatar class="avatar-img" alt="" srcset="">
              <img v-else :src='userImg' class="avatar-img" alt="" srcset="">
              <div class="friends-name">{{ friend.TelegramUser?.first_name }}</div>
            </div>
            <div class="friends-num">Points:{{ friend.TelegramUser?.task_points + friend.TelegramUser?.game_points}}</div>
          </li>
        </ul>
        <div class="nodata" v-if="!friends.length">No Data!</div>
      </div>
      <div class="friends-header">
        <div class="friends-title">Invite Rewards</div>
        <div class="friends-des">You have invited {{count}}</div>
        <button class="friends-refresh friends-copy" ref="copyButton"></button>
      </div>
      <div class="friends-gift">
        <ul>
          <li v-for="(friend, index) in friendsGift" 
          :key="index" 
          :class="{ 'active': friend.TaskFinished?.telegramId, 'goclaimed': count >= friend.Task?.points }"
          @click="claimPoints(friend.TaskFinished, index, friend.Task?.ID)"
          >
            <div class="iscomplete" :class="{ 'iscompleteok': friend.TaskFinished?.isClaimed }"><span></span></div>
            <div class="gift-tdes"><span>{{ friend.Task?.title }}</span></div>
            <div class="gift-num">invite {{ friend.Task?.attrValue }}</div>
            <div class="gift-prize">
              <div class="prize-name">+{{ friend.Task?.points }} <br> Points</div>
            </div>
          </li>
        </ul>
      </div>
      <div class="friends-footer">
        <div class="friends-btn" @click="goTelegramBot()">
          <img src="@/assets/img/friends/user-img.svg" class="btn-img" alt="" srcset="">
          <div>Invite a friend</div>
        </div>
        <!-- <div class="friends-copy"></div> -->
      </div>
    </div>
    <nav-menu on="friends" :showValue="false"></nav-menu>
  </div>
</template>

<script>
import { ref } from 'vue';
import NavMenu from '@/components/NavMenu.vue'
import Clipboard from 'clipboard';
import {get, post} from "@/lib/API";


const routeUrl = process.env.ROUTE_URL || '../';
export default {
  name: 'Friends',
  components: {
    NavMenu
  },
  data() {
    return {
      fixedText: process.env.TG_LINK || '',
      phone: localStorage.getItem("phone"),
      userImg: routeUrl + 'assets/img/friends/user-img.svg',
      friends: [],
      page: 1,
      pageSize: 10,
      count: 0,
      isUpdata: false,
      friendsGift: [],
    }
  },
  setup() {
    return {
    }
  },
  mounted() {
    this.initializeGame();
    this.getMyFriendList(1);
    this.getMyTaskList();
    this.clipboard = new Clipboard(this.$refs.copyButton, {
      text: () => this.fixedText + '?start=' + this.phone
    });
    this.clipboard.on('success', (e) => {
      this.$toast.success('Successfully copied the sharing link!');
      e.clearSelection();
    });

    this.clipboard.on('error', (e) => {
      console.error('Failed to copy text!');
    });
  },
  beforeDestroy() {
    this.clipboard.destroy();
  },
  methods: {
    handleScroll(event) {
      const div = event.target;
      if (div.scrollTop + div.clientHeight >= div.scrollHeight) {
        // 用户已经滚动到底部，加载更多数据
        console.log('load more data')
        this.loadNextPage();
      }
    },
    initializeGame() {
      console.log('initialize game');
    },
    goTelegramBot() {
      const url = process.env.TG_LINK || '';
      const text = `  Play with me, become cryptoexchange CEO and get a token airdrop! \n
  💸  2k Coins as a first-time gift \n
  🔥  25k Coins if you have Telegram Premium`;
      const encodedUrl = encodeURIComponent(url);
      const encodedText = encodeURIComponent(text);
      const telegramShareUrl = `https://telegram.me/share/url?url=${encodedUrl}&text=${encodedText}`;
      window.open(telegramShareUrl, '_blank');
    },
    getMyFriendList(page) {
      this.page = page;
      this.isUpdata = true;
      const timer = setTimeout(() => {
        this.isUpdata = false;
        clearTimeout(timer);
      }, 1000);
      get('/friend/getMyFriendList', { page: page, pageSize: this.pageSize, type: 'friend' })
        .then(response => {
          if (response.code === 0) {
            this.friends = response.data.list || [];
            this.count = response.data.total;
          }
        })
        .catch(error => {
          console.log(error)
        });
    },
    async loadNextPage() {
      if (this.page * this.pageSize < this.count) {
        this.page += 1;
        const response = await get('/friend/getMyFriendList', { page: this.page, pageSize: this.pageSize });
        if (response.code === 0) {
          this.friends.push(...response.data.list)
        }
      }
    },
    getMyTaskList() {
      get('/task/getMyTaskList', { page: 1, pageSize: 10, type: 'friend' })
        .then(response => {
          if (response.code === 0) {
            console.log(response.data)
            this.friendsGift = response.data.list || [];
          }
        })
        .catch(error => {
          console.log(error)
        });
    },
    claimPoints(taskFinished, index, taskId) {
      console.log('claim points', taskFinished.telegramId)
      if (!taskFinished.isClaimed) {
        this.taskClaimPoints(taskId);
        this.friendsGift[index].TaskFinished.telegramId = true;
        const timer = setTimeout(() => {
          this.friendsGift[index].TaskFinished.telegramId = '';
          clearTimeout(timer);
        }, 700);
      } else {
        this.taskClaimPoints(taskId);
      }
    },
    taskClaimPoints(taskId) {
      get('/task/claimPoints', { task_id: taskId,  })
        .then(response => {
          if (response.code === 0) {
            console.log(response.data)
          } else {
            console.log(response)
            this.$toast.error(response.msg);
          }
        })
        .catch(error => {
          console.log(error)
        });
    },
  }
}
</script>

