  display: none;
}
.highestscore {
  font-size: 0.8rem;
  margin-bottom: 1rem;
}
.logo {
  width: 100vw;
  height: 30vh;
  background: url('@/assets/img/quest/tappop_preview.png') no-repeat center center/contain;
}
.btn {
  width: 17rem;
  height: 7rem;
  background: url("@/assets/img/icons/start-game.png") no-repeat center
    center/contain;
}
.restart-btn {
  background-image: url("@/assets/img/icons/try-again.png");
}

#final-score {
  width: 100vw;
  height: 21rem;
  background: url("@/assets/img/icons/score_bg.png") no-repeat center center/83%,
    url("@/assets/img/icons/light.png") no-repeat center center/108%;
  .hint {
    margin-top: 5.5rem;
    color: #0c3ded;
    font-weight: bold;
    font-size: 1.1rem;
  }
  .score {
    font-size: 6rem;
    color: #0c3ded;
    font-weight: 600;
    line-height: 11rem;
  }
  
  #final-score {
    width: 100vw;
    height: 21rem;
    background: url('@/assets/img/icons/score_bg.png') no-repeat center center/75%,
    url('@/assets/img/icons/light.png') no-repeat center center/100%;
    .hint {
      margin-top: 5.8rem;
      color: #0C3DED;
      font-weight: bold;
      font-size: 1.1rem;
    }
    .score{
      font-size: 6rem;
      color: #0C3DED;
      font-weight: 600;
      line-height: 11rem;
    }
  }
  .restartPage{
    justify-content: start;
    padding-top: 2rem;
  }
}
</style>
