              </div>
            </div>
            <div class="three-user-bg">
              <div>2</div>
            </div>
          </div>
          <div class="three-user medal-one">
            <div class="two-img">
              <img
                v-if="top3List[0] && top3List[0].image"
                :src="top3List[0].image"
                alt=""
                srcset=""
              />
              <img
                v-else
                src="@/assets/img/friends/user-img.svg"
                alt=""
                srcset=""
              />
            </div>
            <div class="two-inner">
              <div class="two-name">
                {{ top3List[0] ? top3List[0].username : "暂无数据" }}
              </div>
              <div class="two-points">
                {{ top3List[0] ? top3List[0].score + " Points" : "暂无数据" }}
              </div>
            </div>
            <div class="three-user-bg">
              <div>1</div>
            </div>
          </div>
          <div class="three-user medal-three">
            <div class="two-img">
              <img
                v-if="top3List[2] && top3List[2].image"
                :src="top3List[2].image"
                alt=""
                srcset=""
              />
              <img
                v-else
                src="@/assets/img/friends/user-img.svg"
                alt=""
                srcset=""
              />
            </div>
            <div class="two-inner">
              <div class="two-name">
                {{ top3List[2] ? top3List[2].username : "暂无数据" }}
              </div>
              <div class="two-points">
                {{ top3List[2] ? top3List[2].score + " Points" : "暂无数据" }}
              </div>
            </div>
            <div class="three-user-bg">
              <div>3</div>
            </div>
          </div>
        </div>
      </div>
      <div class="ranking-nav">
        <div class="ranking-nav-inner">
          <div class="ranking-nav-item">
            <div
              class="nav-item"
              v-for="(item, index) in dailyList"
              :key="index"
              :class="{ active: item.id === itemType }"
              @click="itemTypeFn(item.id)"
            >
              {{ item.title }}
            </div>
          </div>
        </div>
      </div>
      <div class="ranking-list">
        <ul v-if="itemType === 1">
          <li
            v-for="(item, index) in basicList"
            :key="index"
            :class="{
              youactive:
                userScoreRank &&
                userScoreRank.rank > 3 &&
                item.rank === userScoreRank.rank,
            }"
          >
            <div class="item-img">{{ item.rank }}</div>
            <div class="user-img">
              <img
                v-if="!item.image"
                src="@/assets/img/friends/user-img.svg"
                alt=""
              />
              <img v-else :src="item.image" alt="" />
            </div>
            <div class="user-name">
              <span>{{ item.username }}</span>
            </div>
            <div class="user-points">{{ item.score }} Points</div>
            <div class="case-img"></div>
          </li>
        </ul>
        <ul v-if="itemType === 2">
          <li
            v-for="(item, index) in friendsList"
            :key="index"
            :class="{ youactive: item.id === 28 }"
          >
            <div class="item-img">{{ item.rank }}</div>
            <div class="user-img">
              <img
                v-if="!item.image"
                src="@/assets/img/friends/user-img.svg"
                alt=""
              />
              <img v-else :src="item.image" alt="" />
            </div>
            <div class="user-name">
              <span>{{ item.username }}</span>
            </div>
            <div class="user-points">{{ item.score }} Points</div>
            <div class="case-img"></div>
          </li>
        </ul>
      </div>
      <div class="ranking-list my-list">
        <ul v-if="itemType === 1 && userScoreRank && userScoreRank.rank !== -1">
          <li>
            <div class="item-img">{{ userScoreRank.rank }}</div>
            <div class="user-img">
              <img
                v-if="!userScoreRank.image"
                src="@/assets/img/friends/user-img.svg"
                alt=""
              />
              <img v-else :src="userScoreRank.image" alt="" />
            </div>
            <div class="user-name">You</div>
            <div class="user-points">{{ userScoreRank.score }} Points</div>
            <div class="case-img"></div>
          </li>
        </ul>
        <ul v-if="itemType === 2 && friendsList.length > 10">
          <li v-for="(item, index) in myList" :key="index">
            <div class="item-img">3+</div>
            <div class="user-img">
              <img
                v-if="!userScoreRank.image"
                src="@/assets/img/friends/user-img.svg"
                alt=""
              />
              <img v-else :src="userScoreRank.image" alt="" />
            </div>
            <div class="user-name">You</div>
            <div class="user-points">{{ item.Points }} Points</div>
            <div class="case-img"></div>
          </li>
        </ul>
      </div>
    </div>
    <nav-menu on="ranking"></nav-menu>
  </div>
</template>

<script>
import { ref } from "vue";
import NavMenu from "@/components/NavMenu.vue";
import Web3 from "web3";
import RewardDistributorAbi from "@/abis/RewardDistributorAbi";
import { get, post } from "@/lib/API";
import {
  useWeb3Modal,
  useWeb3ModalAccount,
  useSwitchNetwork,
  useWeb3ModalProvider,
} from "@web3modal/ethers/vue";
import { BrowserProvider, Contract, formatUnits } from "ethers";

const routeUrl = process.env.ROUTE_URL || "../";
const payuContractAddress = process.env.CONTRACT_ADDRESS;

export default {
  name: "Ranking",
  components: {
    NavMenu,
  },
  data() {
    return {
      itemType: 1,
      basicList: [],
      friendsList: [],
      myList: [
        {
          name: "Novice",
          avatar: routeUrl + "assets/img/ranking/4.png",
          id: 1,
          ranking: 1,
          Points: 5,
        },
      ],
      userAddress: localStorage.getItem("userAddress"),
      seasonDate: "",
      userScoreRank: null,
      globalStart: 1,
      globalEnd: 10,
      friendStart: 1,
      friendEnd: 10,
      globalTop3List: [],
      friendTop3List: [],
      globalIsNotData: false,
      friendIsNotData: false,
    };
  },
  computed: {
    top3List() {
      return this.itemType === 1 ? this.globalTop3List : this.friendTop3List;
    },
  },
  setup() {
    const dailyList = [
      { title: "Global", id: 1, active: true },
      { title: "Friends", id: 2, active: false },
    ];
    return {
      dailyList,
    };
  },
  mounted() {
    this.getSeasons();
    this.getLeaderBoards();
    this.getFriendLeaderBoards();
    this.getMyRankScore();
    this.initializeGame();
  },
  methods: {
    async getSeasons() {
      const res = await get("/season/get_seasons");
      if (res.code === 0) {
        this.seasonDate = res.data[0].name;
      }
    },
    async getMySign() {
      const that = this;
      try {
        const getUseraddress = await this.initAccounts();
        if (getUseraddress) {
          post("/distribution/claimReward", {
            seasonId: 1,
            userAddress: getUseraddress,
          })
            .then((response) => {
              if (response.code === 0) {
                const { seasonId, reward_list, unique_id, sign } =
                  response.data;
                if (reward_list && reward_list.length > 0) {
                  const getRewardList = that.rewardList(reward_list);
                  if (
                    seasonId &&
                    getRewardList.length > 0 &&
                    getUseraddress &&
                    unique_id &&
                    sign
                  ) {
                    // 支付方法一
                    // that.userClaimTransaction(seasonId, getRewardList, getUseraddress, unique_id, sign);
                    // 支付方法二
                    // that.gosendTransaction(seasonId, getRewardList, getUseraddress, unique_id, sign);
                    // walletconnect的支付支付方法
                    this.getContract(
                      seasonId,
                      getRewardList,
                      getUseraddress,
                      unique_id,
                      sign
                    );
                  }
                }
              }
            })
            .catch((error) => {
              console.log(error);
            });
        }
      } catch (err) {
        console.log(err);
      }
    },
    rewardList(list) {
      const web3 = new Web3(window.ethereum);
      return list.map((item) => {
        // const weiValue = web3.utils.toWei(item.amount, 'ether')
        return [item.tokenAddress, item.amount];
      });
    },
    initializeGame() {
      console.log("initialize game");
    },
    itemTypeFn(value) {
      this.itemType = value;
    },
    async initAccounts() {
      // 浏览器获取钱包方法一
      // try {
      //   const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
      //   if ( accounts.length > 0 ) {
      //     return accounts[0];
      //   }
      // } catch (err) {
      // }
      // walletconnect授权钱包方法二
      const { address, chainId, isConnected } = useWeb3ModalAccount();
      if (!address.value) {
        const getModal = useWeb3Modal();
        getModal.open();
      }
      if (isConnected && address.value) {
        return address.value;
      }
    },
    async userClaimTransaction(
      season,
      receiverInfos,
      userAddress,
      uniqueId,
      sign
    ) {
      console.log(
        "userClaimTransaction",
        season,
        receiverInfos,
        userAddress,
        uniqueId,
        sign
      );
      const web3 = new Web3(window.ethereum);
      console.log("web3", web3);
      const contract = new web3.eth.Contract(
        RewardDistributorAbi,
        payuContractAddress
      );
      console.log(contract);
      try {
        await contract.methods
          .distribute(season, receiverInfos, userAddress, uniqueId, sign)
          .send({ from: userAddress, gas: 500000 })
          .on("receipt", (receipt) => {
            console.log("Transaction receipt:", receipt);
            if (receipt.type === "3n") {
              this.userPaid(true, receipt.transactionHash, uniqueId);
            }
          })
          .on("error", (error) => {
            // console.error("Error calling distribute:", error);
            // Get the transaction hash if available
            // const transactionHash = error?.receipt?.transactionHash || null;
            // this.userPaid(false, transactionHash, uniqueId);
          });
      } catch (error) {
        console.error("Error calling distribute:", error);
        // Get the transaction hash if available
        const transactionHash = error?.receipt?.transactionHash || null;
        this.userPaid(false, transactionHash, uniqueId);
      }
    },
    async gosendTransaction(
      seasonId,
      getRewardList,
      getUseraddress,
      unique_id,
      sign
    ) {
      const web3 = new Web3(window.ethereum);
      const contract = new web3.eth.Contract(
        RewardDistributorAbi,
        payuContractAddress
      );
      const gasPrice = await web3.eth.getGasPrice();
      try {
        const transaction = {
          gasPrice: gasPrice,
          gas: "500000",
          from: getUseraddress,
          to: payuContractAddress,
          data: contract.methods
            .distribute(
              seasonId,
              getRewardList,
              getUseraddress,
              unique_id,
              sign
            )
            .encodeABI(), // Contract functions and parameters
        };
        console.log(transaction);
        web3.eth
          .sendTransaction(transaction)
          .on("transactionHash", function (hash) {
            console.log(hash);
          })
          .on("receipt", function (receipt) {
            console.log(receipt);
          })
          .on("confirmation", function (confirmationNumber, receipt) {
            console.log(confirmationNumber, receipt);
          })
          .on("error", function (receipt) {
            console.log(receipt);
          }); // If a out of gas error, the second parameter is the receipt.
      } catch (error) {
        console.error("Error:", error);
      }
    },
    async getContract(season, receiverInfos, userAddress, uniqueId, sign) {
      const myContractAddress = process.env.CONTRACT_ADDRESS;
      const { walletProvider, walletProviderType } = useWeb3ModalProvider();
      const { address, chainId, isConnected } = useWeb3ModalAccount();
      if (isConnected && address.value && walletProvider.value) {
        const providera = new BrowserProvider(walletProvider.value);
        const signer = await providera.getSigner();
        const contract = new Contract(
          myContractAddress,
          RewardDistributorAbi,
          signer
        );
        try {
          console.log(address.value);
          const tx = await contract.distribute(
            season,
            receiverInfos,
            address.value,
            uniqueId,
            sign
          );
          console.log("Transaction hash:", tx.hash);
          const receipt = await tx.wait();
          console.log("Transaction receipt:", receipt);
        } catch (error) {
          console.error("Error calling distribute:", error);
        }
      } else {
        console.log("Not connected");
        const getModal = useWeb3Modal();
        getModal.open();
      }
    },
    async getMyRankScore() {
      const res = await get("/season/get_my_rank", {});
      if (res.code === 0) {
        this.userScoreRank = res.data;
      }
    },
    async userPaid(isSuccess, hash, uniqueId) {
      const res = await post("/distribution/userPaid", {
        is_success: isSuccess,
        tx_hash: hash,
        unique_id: uniqueId,
      });
      if (res.code === 0) {
        console.log("userPaid success", res.data);
      }
    },
    async getLeaderBoards() {
      if (this.globalIsNotData) return;
      const res = await post("/season/get_leaderboards", {
        start: this.globalStart,
        end: this.globalEnd,
      });
      if (res.code === 0) {
        if (!res.data.lb || res.data.lb.length < 10)
          this.globalIsNotData = true;
        this.basicList = [...this.basicList, ...res.data.lb];
        this.globalTop3List = res.data.top3 || [];
      }
    },
    async getFriendLeaderBoards() {
      if (this.friendIsNotData) return;
      const res = await post("/season/get_friend_leaderboards", {
        start: this.friendStart,
        end: this.friendEnd,
      });
      if (res.code === 0) {
        if (!res.data.friend_lb || res.data.friend_lb.length < 10)
          this.friendIsNotData = true;
        this.friendsList = [...this.friendsList, ...res.data.friend_lb];
        this.friendTop3List = res.data.friend_top3 || [];
      }
    },
    handleScroll(event) {
      const div = event.target;
      if (div.scrollTop + div.clientHeight >= div.scrollHeight) {
        // 用户已经滚动到底部，加载更多数据
        if (this.itemType === 1 && !this.globalIsNotData) {
          this.globalStart = this.globalStart + 10;
          this.globalEnd = this.globalEnd + 10;
          this.getLeaderBoards();
        } else if (this.itemType === 2 && !this.friendIsNotData) {
          this.friendStart = this.friendStart + 10;
          this.friendEnd = this.friendEnd + 10;
          this.getFriendLeaderBoards();
        }
      }
    },
  },
};
</script>

